@use 'variables' as v;
@use 'mixins' as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
 margin:0;
  padding: 0;
  border:0;
}

body {
  font-family: v.$font-primary;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}